import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DataSKHDTComponent } from './data-skhdt.component';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { MaterialModule } from 'src/app/dealer/material.module';

const routes: Routes = [
  {
    path: '',
    component: DataSKHDTComponent,
  },
];

@NgModule({
  declarations: [DataSKHDTComponent],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    MaterialModule,
    FormsModule,
    RouterModule.forChild(routes),
  ],
  exports: [RouterModule, MaterialModule],
})
export class DataSKHDTModule {}
